/* Default styles for larger screens */

.mission-content {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 70vh;
  background-color: #3b71ca;
  overflow: hidden;
  position: relative;
}

.particle {
  width: 100px;
  height: 100px;
  top: -30%;
  background-color: white;
  position: absolute;
  border-radius: 50%;
  animation: fall 15s infinite;
  opacity: 0.7;
}

@keyframes fall {
  0% {
    top: -30%;
  }
  20% {
    top: -3%;
  }
  80% {
    top: 85%;
  }
  100% {
    top: 100%;
  }
}

.particle:nth-of-type(2n) {
  width: 60px;
  height: 60px;
}

.particle:nth-of-type(3n) {
  width: 120px;
  height: 120px;
}

.particle:nth-of-type(1) {
  left: 10%;
  animation-delay: 1s;
}

.particle:nth-of-type(2) {
  left: 60%;
  animation-delay: 2s;
}

.particle:nth-of-type(3) {
  left: 15%;
  animation-delay: 3s;
}

.particle:nth-of-type(4) {
  left: 95%;
  animation-delay: 4s;
}

.particle:nth-of-type(5) {
  left: 85%;
  animation-delay: 5s;
}

.particle:nth-of-type(6) {
  left: 50%;
  animation-delay: 6s;
}

.particle:nth-of-type(7) {
  left: 65%;
  animation-delay: 7s;
}

.particle:nth-of-type(8) {
  left: 35%;
  animation-delay: 9s;
}

.particle:nth-of-type(9) {
  left: 5%;
  animation-delay: 9s;
}

.particle:nth-of-type(10) {
  left: 75%;
  animation-delay: 10s;
}

.particle:nth-of-type(11) {
  left: 35%;
  animation-delay: 11s;
}

.particle:nth-of-type(12) {
  left: 60%;
  animation-delay: 12s;
}

.particle:nth-of-type(13) {
  left: 90%;
  animation-delay: 13s;
}
.particle:nth-of-type(14) {
  left: 35%;
  animation-delay: 14s;
}

.particle:nth-of-type(15) {
  left: 65%;
  animation-delay: 15s;
}

.border-goo1 {
  position: absolute;
  top: 0;
  left: 0;
  background-color: white;
  width: 100%;
  height: 4%;
}

.border-goo2 {
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: white;
  width: 100%;
  height: 4%;
}

.mission-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30vw;
  height: 40vh;
  background-color: transparent;
  padding: 1rem;
  color: white;
}

.mission-icon {
  font-size: 6rem;
  transition: 0.8s ease;
}

.mission-icon:hover {
  color: #332d2d;
}

.mission-span {
  position: relative;
  font-weight: bold;
  font-size: 3rem;
  -webkit-text-stroke: 2px white;
  color: #3b71ca;
  text-transform: uppercase;
}

.mission-span::before {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  color: #332d2d;
  text-decoration: underline;
  -webkit-text-stroke: 0 #332d2d;
  overflow: hidden;
  transition: 2.5s ease;
}

.mission-span:hover::before {
  border-right: 2px solid #332d2d;
  width: 100%;
}

.mission-p {
  margin-top: 1rem;
  text-align: center;
  font-size: 1.2rem;
  transition: 0.5s ease;
}

.mission-p:hover {
  color: #332d2d;
}

/* Media query for smaller screens */
@media (max-width: 1350px) {
  .mission-content {
    flex-wrap: wrap;
    margin-bottom: 20px;
    height: auto;
  }

  .border-goo1 {
    display: none;
  }

  .border-goo2 {
    height: 5%;
  }

  .mission-card {
    width: 90vw;
    height: auto;
    margin-bottom: 20px; /* Add some space between cards on smaller screens */
  }
}

/* Media Query for phones */
@media (max-width: 768px) {
  .mission-content {
    width: 100%;
    flex-direction: column;
    margin-bottom: 20px;
    height: auto;
  }

  .border-goo1 {
    display: none;
  }

  .border-goo2 {
    height: 5%;
  }

  .mission-card {
    width: 100%;
    height: auto;
    margin-bottom: 20px; /* Add some space between cards on smaller screens */
  }
}
