.campus-card {
  position: relative;
  width: 100%;
  height: 100px;
  margin: auto;
  border-radius: 14px;
  z-index: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 7px 0 rgba(225, 255, 255, 0.5);
}

.campus-bg {
  position: absolute;
  inset: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 2;
  background-color: rgba(36, 37, 38, 0.89);
  border-radius: 10px;
  overflow: hidden;
  outline: 2px solid #b0b3b8;
}

.campus-blob {
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background-color: #f02849;
  opacity: 1;
  -webkit-filter: blur(12px);
          filter: blur(12px);
  -webkit-animation: blob-bounce 5s infinite ease;
          animation: blob-bounce 5s infinite ease;
}

@-webkit-keyframes blob-bounce {
  0% {
    -webkit-transform: translate(-100%, -100%) translate3d(0, 0, 0);
            transform: translate(-100%, -100%) translate3d(0, 0, 0);
  }

  25% {
    -webkit-transform: translate(-100%, -100%) translate3d(100%, 0, 0);
            transform: translate(-100%, -100%) translate3d(100%, 0, 0);
  }

  50% {
    -webkit-transform: translate(-100%, -100%) translate3d(100%, 100%, 0);
            transform: translate(-100%, -100%) translate3d(100%, 100%, 0);
  }

  75% {
    -webkit-transform: translate(-100%, -100%) translate3d(0, 100%, 0);
            transform: translate(-100%, -100%) translate3d(0, 100%, 0);
  }

  100% {
    -webkit-transform: translate(-100%, -100%) translate3d(0, 0, 0);
            transform: translate(-100%, -100%) translate3d(0, 0, 0);
  }
}

@keyframes blob-bounce {
  0% {
    -webkit-transform: translate(-100%, -100%) translate3d(0, 0, 0);
            transform: translate(-100%, -100%) translate3d(0, 0, 0);
  }

  25% {
    -webkit-transform: translate(-100%, -100%) translate3d(100%, 0, 0);
            transform: translate(-100%, -100%) translate3d(100%, 0, 0);
  }

  50% {
    -webkit-transform: translate(-100%, -100%) translate3d(100%, 100%, 0);
            transform: translate(-100%, -100%) translate3d(100%, 100%, 0);
  }

  75% {
    -webkit-transform: translate(-100%, -100%) translate3d(0, 100%, 0);
            transform: translate(-100%, -100%) translate3d(0, 100%, 0);
  }

  100% {
    -webkit-transform: translate(-100%, -100%) translate3d(0, 0, 0);
            transform: translate(-100%, -100%) translate3d(0, 0, 0);
  }
}

.campus-scene {
  width: 100%;
