.flip-card {
  position: relative;
  color: black;
  font-family: Arial, Helvetica, sans-serif;
  display: inline-flex;
  flex-direction: column;
  box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.2);
  border-radius: 0.1em;
  margin: 0.2rem;
  z-index: 9999;
  font-size: 7rem;
}

.flip-card-top,
.flip-card-bottom,
.flip-card.flip::before,
.flip-card.flip::after {
  height: 0.75em;
  line-height: 1;
  padding: 0.25em;
  overflow: hidden;
}

.flip-card-top,
.flip-card.flip::before {
  background-color: #f7f7f7;
  border-top-right-radius: 0.15em;
  border-top-left-radius: 0.15em;
  border-bottom: 2px solid rgb(0, 0, 0, 0.1);
}

.flip-card-bottom,
.flip-card.flip::after {
  background-color: white;
  display: flex;
  align-items: flex-end;
  border-bottom-right-radius: 0.1em;
  border-bottom-left-radius: 0.1em;
}

.flip-card.flip::before {
  content: attr(data-value);
  position: absolute;
  width: 100%;
  -webkit-animation: flip-top 250ms ease-in;
          animation: flip-top 250ms ease-in; /* 250ms is the org */
  -webkit-transform-origin: bottom;
          transform-origin: bottom;
}

@-webkit-keyframes flip-top {
  100% {
    -webkit-transform: rotateX(90deg);
            transform: rotateX(90deg);
  }
}

@keyframes flip-top {
  100% {
    -webkit-transform: rotateX(90deg);
            transform: rotateX(90deg);
  }
}

.flip-card.flip::after {
  content: attr(data-value);
  position: absolute;
  bottom: 0;
  width: 100%;
  -webkit-animation: flip-bttm 250ms ease-out 250ms;
          animation: flip-bttm 250ms ease-out 250ms; /* 250ms is the org */
  -webkit-transform-origin: top;
          transform-origin: top;
  -webkit-transform: rotateX(90deg);
          transform: rotateX(90deg);
}

@-webkit-keyframes flip-bttm {
  100% {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }
}

@keyframes flip-bttm {
  100% {
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
  }
}

@media (max-width: 1200px) {
  .flip-card {
    font-size: 4rem;
    margin: 0.1rem;
  }
}

