.loader {
  --s: 30px;

  --_d: calc(0.353 * var(--s));
  width: calc(var(--s) + var(--_d));
  aspect-ratio: 1;
  display: grid;
}
.loader:before,
.loader:after {
  content: "";
  grid-area: 1/1;
  -webkit-clip-path: polygon(
    var(--_d) 0,
    100% 0,
    100% calc(100% - var(--_d)),
    calc(100% - var(--_d)) 100%,
    0 100%,
    0 var(--_d)
  );
          clip-path: polygon(
    var(--_d) 0,
    100% 0,
    100% calc(100% - var(--_d)),
    calc(100% - var(--_d)) 100%,
    0 100%,
    0 var(--_d)
  );
  background: conic-gradient(
    from -90deg at calc(100% - var(--_d)) var(--_d),
    #7bc9ff 135deg,
    #1c1678 0 270deg,
    #8576ff 0
  );
  -webkit-animation: l6 2s infinite;
          animation: l6 2s infinite;
}
.loader:after {
  -webkit-animation-delay: -1s;
          animation-delay: -1s;
}
@-webkit-keyframes l6 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  25% {
    -webkit-transform: translate(40px, 0);
            transform: translate(40px, 0); /* Increased translation */
  }
  50% {
    -webkit-transform: translate(40px, 50px);
            transform: translate(40px, 50px); /* Increased translation */
  }
  75% {
    -webkit-transform: translate(0, 50px);
            transform: translate(0, 50px); /* Increased translation */
  }
  100% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}
@keyframes l6 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  25% {
    -webkit-transform: translate(40px, 0);
            transform: translate(40px, 0); /* Increased translation */
  }
  50% {
    -webkit-transform: translate(40px, 50px);
            transform: translate(40px, 50px); /* Increased translation */
  }
  75% {
    -webkit-transform: translate(0, 50px);
            transform: translate(0, 50px); /* Increased translation */
