    background-position-x: -1000px;
  }
}

.wrappers {
  position: relative;
  width: 80vw;
  height: 37vh;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 20px;
  padding: 1.5rem;
  box-shadow: 0px 0px 15px 3px rgba(0, 0, 0, 0.5);
}

.login-form {
  width: 30svw;
  height: 35vh;
  -webkit-transform: translate(10%, -150%);
          transform: translate(10%, -150%);
}

.isLock {
  position: relative;
}

.isLock-eye {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 1.5rem;
  border: none;
  background-color: transparent;
}

.register {
  border: none;
  background-color: transparent;
  color: #0275d8;
}

.register:hover {
  text-decoration: underline;
}

.box {
  position: relative;
  z-index: 1000;
  width: 35vw;
  height: 50vh;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 20px;
  -webkit-transform: translate(105%, -20%);
          transform: translate(105%, -20%);
  box-shadow: 0px 0px 15px 3px rgba(0, 0, 0, 0.5);
}

.box::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 34vw;
  height: 48vh;
  background-color: transparent;
  background-image: url(/static/media/loginimg.4d6f62e2.png);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 20px;
  -webkit-transform: translate(1.5%, 2%);
          transform: translate(1.5%, 2%);

  /* Box Shadow (Outer Shadow) */
  /* box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5); */

  /* Inset Box Shadow */
  /* Uncomment the line below to add an inset box shadow */
  box-shadow: inset 0px 0px 5px 2px rgba(0, 0, 0, 1);
}

.hidingboy {
  position: absolute;
  width: 10vw;
  height: 20vh;
  background-image: url(/static/media/hidingboy.74821058.png);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  -webkit-transform: translate(691%, -200%);
          transform: translate(691%, -200%);
}

/* Media query for smaller screens */
@media screen and (max-width: 1180px) {
  .login-contents {
    height: 100vh;
  }

  .wrappers {
    width: 80vw;
    height: auto;
  }

  .login-form {
    width: 90%;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }

  .box {
    display: none;
  }

  .hidingboy {
    display: none;
  }
}

/* Media Query for phones */
@media (max-width: 768px) {
  .wrappers {
    width: 90%;
    height: auto;
  }

  .login-form {
    width: 100%;
  }

  .box {
    width: 80%;
    height: auto;
    -webkit-transform: translate(10%, -20%);
            transform: translate(10%, -20%);
  }

  .hidingboy {
    width: 20%;
    -webkit-transform: translate(50%, -150%);
            transform: translate(50%, -150%);
  }
}

