.login-contents {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60vh;
  background: linear-gradient(to top, #ffecd6, #3b71ca, #3b71ca, #3b71ca);
  overflow: hidden;
}

.wave {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100px;
  background-image: url("../../../assets/landing/wave.png");
  background-size: 1000px 100px;
}

.wave.wave1 {
  animation: animate 30s linear infinite;
  z-index: 1000;
  opacity: 1;
  animation-delay: 0s;
  bottom: 0;
}
.wave.wave2 {
  animation: animate2 15s linear infinite;
  z-index: 999;
  opacity: 0.5;
  animation-delay: -5s;
  bottom: 20px;
}
.wave.wave3 {
  animation: animate 30s linear infinite;
  z-index: 998;
  opacity: 0.2;
  animation-delay: -2s;
  bottom: 15px;
}

.wave.wave4 {
  animation: animate2 5s linear infinite;
  z-index: 997;
  opacity: 0.7;
  animation-delay: -5s;
  bottom: 20px;
}

@keyframes animate {
  0% {
    background-position-x: 0;
  }
  100% {
    background-position-x: 1000px;
  }
}

@keyframes animate2 {
  0% {
    background-position-x: 0;
  }
  100% {
    background-position-x: -1000px;
  }
}

.wrappers {
  position: relative;
  width: 80vw;
  height: 37vh;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 20px;
  padding: 1.5rem;
  box-shadow: 0px 0px 15px 3px rgba(0, 0, 0, 0.5);
}

.login-form {
  width: 30svw;
  height: 35vh;
  transform: translate(10%, -150%);
}

.isLock {
  position: relative;
}

.isLock-eye {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 1.5rem;
  border: none;
  background-color: transparent;
}

.register {
  border: none;
  background-color: transparent;
  color: #0275d8;
}

.register:hover {
  text-decoration: underline;
}

.box {
  position: relative;
  z-index: 1000;
  width: 35vw;
  height: 50vh;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 20px;
  transform: translate(105%, -20%);
  box-shadow: 0px 0px 15px 3px rgba(0, 0, 0, 0.5);
}

.box::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 34vw;
  height: 48vh;
  background-color: transparent;
  background-image: url("../../../assets/landing/loginimg.png");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 20px;
  transform: translate(1.5%, 2%);

  /* Box Shadow (Outer Shadow) */
  /* box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5); */

  /* Inset Box Shadow */
  /* Uncomment the line below to add an inset box shadow */
  box-shadow: inset 0px 0px 5px 2px rgba(0, 0, 0, 1);
}

.hidingboy {
  position: absolute;
  width: 10vw;
  height: 20vh;
  background-image: url("../../../assets/landing/hidingboy.png");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  transform: translate(691%, -200%);
}

/* Media query for smaller screens */
@media screen and (max-width: 1180px) {
  .login-contents {
    height: 100vh;
  }

  .wrappers {
    width: 80vw;
    height: auto;
  }

  .login-form {
    width: 90%;
    transform: translate(0, 0);
  }

  .box {
    display: none;
  }

  .hidingboy {
    display: none;
  }
}

/* Media Query for phones */
@media (max-width: 768px) {
  .wrappers {
    width: 90%;
    height: auto;
  }

  .login-form {
    width: 100%;
  }

  .box {
    width: 80%;
    height: auto;
    transform: translate(10%, -20%);
  }

  .hidingboy {
    width: 20%;
    transform: translate(50%, -150%);
  }
}
