.forgot {
  background: none;
  color: #007bff; /* Link color */
  border: none;
  padding: 0;
  font-weight: bold;
  font: inherit;
  cursor: pointer;
  text-decoration: underline;
}

.forgot:hover {
  color: #0056b3; /* Hover color */
}

.forgot:focus {
  outline: none;
}

.divider {
  display: flex;
  align-items: center;
  text-align: center;
  margin: 20px 0;
}

.divider::before,
.divider::after {
  content: "";
  flex: 1;
  border-bottom: 1px solid black; /* Line color */
}

.divider:not(:empty)::before {
  margin-right: 0.5em;
}

.divider:not(:empty)::after {
  margin-left: 0.5em;
}

.divider-text {
  font-weight: bold;
  font-size: 1em;
}
