.fullName {
  display: flex;
  flex-wrap: wrap;
}

@media screen and (min-width: 390px) and (max-width: 814px) {
  .label {
    font-size: 13px;
  }

  .description {
    font-size: 15px;
  }
}

@media screen and (min-width: 662px) and (max-width: 899px) {
  .label {
    font-size: 15px;
  }

  .description {
    font-size: 10px;
  }
}
.form-control:disabled,
.form-control[readonly] {
  background-color: transparent !important;
}
