.step-label{
    white-space: nowrap;
}

.stepper-horizontal .active .circle {
    display: flex;
    align-items: center;
    justify-content: center;
    animation: big-circle 0.5s ease-in-out forwards;;
    margin-left: 0.5rem;
}

@keyframes big-circle {
    from {
      transform: scale(1);
    }
    to {
      transform: scale(1.2);
    }
  }

.stepper-horizontal .active .small-circle,
.stepper-horizontal .completed .small-circle,
.stepper-horizontal .current .small-circle {
    width: 17px;
    height: 17px;
    border-radius: 50%;
    position: absolute;
    top: 20px;
    background-color: #1ad71a!important;
    z-index: 1;
    
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;

    .fa-check{
        color: white;
    }
}

.icon-cont{
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
}

.stepper-horizontal li::before,
.stepper-horizontal li::after {
    display: none;
}

.stepper-horizontal li {
    flex: none;
    margin-bottom: 10px;
}

.stepper-horizontal {
    width: 90%;
    display: flex;
    align-items: baseline;
    justify-content: center;
    flex-direction: row;
    overflow-x: scroll;
}
  
.step-line {
    flex-grow: 1;
    min-width: 20%;
    height: 2px;
    background-color: #ccc;
}
  
.glow-line{
    background-color: blue; 
}
  
.stepper-horizontal .active .circle {
    background-color: blue; 
    transform: scale(1.2); 
}

.step-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: center;
    align-items: center;

}

ul.stepper {
    max-width: 90%;
    overflow-x: auto;
    justify-content: flex-start;
}
  
@media (max-width: 767px) {
    .stepper-horizontal {
        flex-direction: row;
        width: 70%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-direction: row;
    }
    ul.stepper{
        overflow-x: scroll;
    }
    .step-line {
        flex-grow: 3;
        height: 2px;
        width: 20px;
        background-color: #ccc;
    }

    .stepper-horizontal li::before,
    .stepper-horizontal li::after {
        display: none;
    }
        
    .glow-line{
        background-color: blue; 
    }
    
}