.strand-slider {
  width: 100%;
  height: 29.5vh;
  margin: auto;
  position: relative;
  display: grid;
  place-items: center;
  overflow: hidden;
}

.strand-slide-track {
  display: flex;
  justify-content: center;
  width: 100%;
}

.strand-slide {
  height: 100%;
  width: 20%;
  padding: 5px;
  perspective: 100px;
  border: 2px solid black;
}

.strand-animation {
  animation: strandSlide 20s linear infinite;
}

@keyframes strandSlide {
  0% {
    transform: translateX(calc(350px * var(--i)));
  }
  100% {
    transform: translateX(calc(-250px * var(--i)));
  }
}

.strand-slider::before {
  top: 0;
  left: 0;
}

.strand-slider::after {
  bottom: 0;
  right: 0;
  transform: rotateZ(180deg);
}

.strand-card {
  position: relative;
  text-align: center;
  font-size: 2rem;
  text-transform: uppercase;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.5); /* Adjust this */
  border-radius: 10px;
  box-shadow: 0 1px 2px 3px rgba(0, 0, 0, 0.2);
  transition: all 1s;
}

.strand-population {
  position: relative;
  width: 100%;
  height: auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.strand-half-box-left,
.strand-half-box-right {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 85px;
  aspect-ratio: 1;
}

.strand-half-box-left::before,
.strand-half-box-right::before {
  content: "";
  position: absolute;
  width: 85px;
  height: 85px;
  border: 4px solid black;
  transform: rotate(45deg);
  border-radius: 5px;
}

.strand-half-box-left::before {
  border-top: none;
  border-right: none;
  left: 15%;
}

.strand-half-box-right::before {
  border-bottom: none;
  border-left: none;
  right: 15%;
}

.strand-half-box-left p:first-child,
.strand-half-box-right p:first-child {
  font-size: 2.3rem;
  line-height: 0.9em;
  font-weight: 500;
}

.strand-half-box-left p:nth-child(2),
.strand-half-box-right p:nth-child(2) {
  font-size: 0.9rem;
  line-height: 0.9em;
  font-weight: 400;
}

.strand-total-population {
  font-size: 5.5rem;
  font-weight: 600;
}

/* HTML: <div class="loader"></div> */
.strand-loader {
  position: absolute;
  top: 0;
  left: 31%;
  width: 140px;
  aspect-ratio: 1;
  padding: 10px;
  box-sizing: border-box;
  display: grid;
  background: rgba(17, 17, 17, 0.5);
  filter: blur(5px) contrast(10) hue-rotate(300deg);
  mix-blend-mode: darken;
  z-index: -1;
}
.strand-loader:before,
.strand-loader:after {
  content: "";
  grid-area: 1/1;
  width: 55px;
  height: 55px;
  background: #ffff00;
  animation: l7 2s infinite;
}
.strand-loader:after {
  animation-delay: -1s;
}
@keyframes l7 {
  0% {
    transform: translate(0, 0);
  }
  25% {
    transform: translate(100%, 0);
  }
  50% {
    transform: translate(100%, 100%);
  }
  75% {
    transform: translate(0, 100%);
  }
  100% {
    transform: translate(0, 0);
  }
}
